	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/vector-magic/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://vectormagic.stanford.edu/\" title=\"Open link in a new window\">Vector Magic</a> was recently introduced to me by <a href=\"http://www.spreadshirt.com\" title=\"Open link in a new window\">Spreadshirt</a>.  Essentially, it's an online vector tracing tool, kinda like those bundled with packages like the Adobe suite or other design programs.  It relies on Flex, although I can't quite figure out whether it's Flex or the Vector Magic servers that are doing the heavy lifting - probably a bit of both.  I'm quite interested in this though, and their claims about the awesomeness of their service vs. others are certainly impressive.</p>\n<p>Interface-wise it's functional, pretty standard flex interfaces, which actually inspires confidence that the back-end programming must be pretty hot.  Sofar I've only tested this with simple and unimportant images, but it's real strength is going to be in Vectorisation of things like corporate logos - I just haven't given this a test yet.  But I'd certainly say that it's worth a visit.</p>\n<p><img src=\"/content/images/2007/11/stanfordedu.jpg\" alt=\"stanfordedu.jpg\"></p>","frontmatter":{"title":"Vector Magic","date":"November 11, 2007","tags":["external-links","flex","tools","vectors"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/recent-posts-range-widget/","tags":["php","wordpress","wordpress-plugins"],"title":"Recent Posts Range Widget","date":"2007-11-11T00:58:51.000Z"}},"next":{"frontmatter":{"path":"/blog/development-sneaky-peek/","tags":["flash","air","mdm-zinc","sneaky-peek","work"],"title":"Development sneaky-peek","date":"2007-11-17T11:17:31.000Z"}}}}

